function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);

}

class GlobalWaiting extends fairygui.GComponent {
    constructor() {
        super();
    }

    constructFromXML(xml) {
        super.constructFromXML(xml);
        this._obj = this.getChild("n1");
        this.on(laya.events.Event.DISPLAY, this, this.onAddedToStage);
        this.on(laya.events.Event.UNDISPLAY, this, this.onRemoveFromStage);
    }

    onAddedToStage() {
        Laya.timer.frameLoop(2, this, this.onTimer);
    }

    onRemoveFromStage() {
        Laya.timer.clear(this, this.onTimer);
    }

    onTimer() {
        var i = this._obj.rotation;
        i += 10;
        if (i > 360)
            i = i % 360;
        this._obj.rotation = i;
    }
}
class Win extends fairygui.Window {
    constructor(comName, packageName, closeHandler) {
        super();
        if (typeof packageName == 'function') {
            closeHandler = packageName;
            packageName = null
        }
        this.comName = comName;
        this.packageName = packageName || 'Package1';
        this.closeHandler = closeHandler;
    }
    onInit() {
        this.contentPane = fairygui.UIPackage.createObject(this.packageName, this.comName);
        console.log(this.comName);
        if (!this.contentPane) this.contentPane = fairygui.UIPackage.createObject(this.packageName, 'notImpl');
        if (!this.contentPane) return;

        // window.popup=this;

        this.initBtns();
        this.assignAllBtns();
        if (this.contentPane.width == 540) this.width = Laya.stage.width;
        if (this.contentPane.height == 960) this.height = Laya.stage.height;

        this.center();

        //
        this.setPivot(0.5, 0.5);

        var self = this;

        var bg = this.contentPane.getChildAt(0);
        if (bg instanceof fairygui.GGraph) {
            var maybeBackImage = this.contentPane.getChildAt(1);
            if (maybeBackImage instanceof fairygui.GImage) {
                var layao = maybeBackImage.displayObject;
                layao.mouseEnabled = true;
                layao.mouseThrough = true;
                layao.on('click', Handler.create(null, function() {
                    console.log('donot close');
                }));
            }
            bg.onClick(this, function(event) {
                this.hide();
            });
        }

    }
    initBtns() {}
    doShowAnimation() {
        this.alpha = 0.42;
        laya.utils.Tween.to(this, { alpha: 1 }, 300, laya.utils.Ease.quadOut, Handler.create(this, this.onShown));
    }

    hide() {
        super.hide();
        if (typeof this.closeHandler == 'function') this.closeHandler();

        if (this._h) {
            for (var e in this._h) {
                me.removeListener(e, this._h[e]);
            }
            this._h = {};
        }
    }
    doHideAnimation() {
        //laya.utils.Tween.to(this, { alpha: 0.2 },180, laya.utils.Ease.quadOut, Handler.create(this, this.hideImmediately));
        this.hideImmediately();
    }
    assignAllBtns() {
        'use strict';
        var cl = this.contentPane._children;
        var fairyguiNamedEle = /[A-Za-z]\d+/;
        for (var i = 0; i < cl.length; i++) {
            var btn = cl[i].asButton;
            if (btn instanceof fairygui.GButton) {
                if (fairyguiNamedEle.test(btn.name)) continue;
                if (btn._autoAssign == false) return;
                if (btn.name == 'close' || btn.name == 'closeButton') {
                    btn.onClick(this, this.hide);
                    continue;
                }
                let _n = btn.name.split('.');
                let _idx = _n[1] || 1;
                _n = _n[0];
                let ctrl = this.contentPane.getController(_n);
                if (ctrl) {
                    btn.onClick(this, function() {
                        ctrl.selectedIndex = _idx;
                    });
                    ctrl.selectedIndex(0);
                } else {
                    btn.onClick(this, function() {
                        var candiName = capitalizeFirstLetter(_n) + 'Win';
                        if (wins[candiName]) {
                            var win = new wins[candiName];
                        } else var win = new wins.Win(_n);
                        win.show();
                    })
                }
            }
        }
    }
}

function drawcoin(ndui, ty) {
    Laya.SoundManager.playSound(require('./res/snd/coins.ogg'));
    var z = 0;
    var pox = 484;
    var textureUrl = 'ui://7sthu7yao5uhg';
    if (ndui >= 500 || ty) {
        textureUrl = 'ui://7sthu7yaqjanb9';
        pox = 287;
        if (ndui > 8000) {
            ndui /= 8000;
        } else {
            ndui /= 80;
        }
    };
    if (ndui > 60) {
        ndui = 60
    };
    console.log('drawcoin', ndui);

    function a() {
        setTimeout(
            function() {
                var jinbi = fairygui.UIPackage.createObjectFromURL(textureUrl);
                if (jinbi.getTransition('t0'))
                    jinbi.getTransition('t0').play()
                jinbi.x = 440 + Math.floor(Math.random() * 60);
                jinbi.y = 46; //+274
                fairygui.GRoot.inst.addChild(jinbi);
                baobi(jinbi, pox);
                z++;
                if (z < ndui / 2)
                    a();
            }, 50)
    };
    a();
}

function baobi(jinbi, px) {
    var pox = 0,
        poy = 0;
    for (var x = 0; x < 4; x++) {

        pox = px;
        poy = -270; //+274
    }
    Laya.timer.once(1000, this, function() {
        Laya.Tween.to(jinbi, {
            x: pox,
            y: poy
        }, 900);
    });
    Laya.timer.once(2000, this, function() {
        jinbi.dispose()
    })
}

function sixtySecond(plane) { //60
    var sixty = 60;
    var time = plane.contentPane.getChild('n13')
    var b = function() {
        setTimeout(function() {
            sixty--;
            time.text = sixty.toString();
            if (sixty > 0) {
                b();
            } else {
                if (plane.isShowing) {
                    plane.hide();
                    for (var x = 0; x < Laya.stage._childs.length; x++) {
                        if (Laya.stage.getChildAt(x).name == 'bg1') {
                            Laya.stage.removeChildAt(x);
                        }
                    };
                    _socket.sendp({ c: 'quitgame' })
                }
            }
        }, 1000)
    }
    b();
}

class TipWin extends Win {
    constructor(str, opt) {
        super('TIP');
        if (typeof str == 'string') this._tip = str;
        else if (str instanceof Error) this._tip = str.message;
        if (opt) {
            if (opt instanceof Handler) {
                this._opt = { waitHandler: opt };
            } else this._opt = opt;
        } else this._opt = {};
    }
    onInit() {
        super.onInit();
        if (!this.contentPane) return;
        fairygui.GRoot.inst.closeModalWait();
        this._inited = true;
        // search text
        var text = this.findText(this.contentPane);
        if (text) text.text = (this._tip || 'something wrong').toString();
        // this.contentPane.getChild('n3').getChild('n1').text=(this._tip||'something wrong').toString();
        // if (this._pop) {
        // 	var cb=this._pop.cb;
        // 	this.contentPane.getTransition('t0').play(cb?Handler.create(null, cb):null);
        // }
    }
    findText(com) {
        for (var i = 0; i < com.numChildren; i++) {
            var item = com.getChildAt(i);
            if (item instanceof fairygui.GTextField) return item;
            if (item instanceof fairygui.GComponent) {
                var obj = this.findText(item);
                if (obj) return obj;
            }
        }
    }
    popup(cb) {
        // if (!this.contentPane) return;
        var self = this;
        this.show();
        if (!this.contentPane) {
            console.log('TIP resource not found', this._tip);
            return cb && cb();
        }

        var trans_show = this.contentPane.getTransition('t0'),
            trans_hide = this.contentPane.getTransition('t1');

        function show(cb) {
            cb = cb || function() {};
            if (trans_show) trans_show.play(Handler.create(null, cb));
            else cb();
        }

        function hide(cb) {
            cb = cb || function() {};
            if (trans_hide) trans_hide.play(Handler.create(null, cb));
            else setTimeout(cb, 800);
        }
        if (self._opt.waitHandler) {
            this.modal = true;
            show(function() {
                cb && cb();
                self.onClick(null, function() {
                    self.hide();
                    if (typeof self._opt.waitHandler === 'function') self._opt.waitHandler();
                    else if (self._opt.waitHandler instanceof Handler) self._opt.waitHandler.run();
                });
            });
        } else {
            show();
            hide(function() {
                self.hide();
                cb && cb();
            });
        }
    }
}


