    dailyinit(daily) {
        var self = this;
        var list3 = daily.getChild('n113');
        var pro3 = daily.getChild('n332');
        var dailyBoolen = {
            dailyCharge: false,
            dailyMaxCharge: false,
            dailyCheck: false,
            dailyOnhour: false,
            daily100Million: false,
            daily1000F: false,
            daily5000F: false,
            daily10000F: false,
            online90: false,
            online120: false
        }
        var dailyGetCoin = Math.round(me.dailyGetCoin / 10000);
        var order = ['dailyCharge', 'dailyMaxCharge', 'online90', 'online120', 'dailyCheck', 'dailyOnhour', 'daily100Million', 'daily1000F', 'daily5000F', 'daily10000F'];
        var orderText = ['dailyBoolen.dailyCharge', 'me.dailyCharge', 'onlineTime', 'onlineTime', 'dailyBoolen.dailyCheck', 'dailyBoolen.dailyOnhour', 'dailyGetCoin', 'me.dailyGetFishes', 'me.dailyGetFishes', 'me.dailyGetFishes']
        var points = [20, 40, 10, 25, 5, 10, 15, 20, 25, 40];
        var action = ['shop', 'shop', 0, 0, 'qian', 'onhour', 'game', , 'game', 'game', 'game'];
        if (me.dailyCharge && me.dailyCharge > 0) {
            dailyBoolen.dailyCharge = true;
        }
        if (me.dailyCharge && me.dailyCharge >= 200) {
            dailyBoolen.dailyMaxCharge = true;
        }
        if (me.month) {
            if (me.month[(this.today - 1)] == 2) {
                dailyBoolen.dailyCheck = true;
            }
        }
        for (var z = 0; z < 6; z++) {
            if (me.onhour && me.onhour[z]) {
                dailyBoolen.dailyOnhour = true;
            }
        }
        if (me.dailyGetCoin && me.dailyGetCoin >= 1000000) {
            dailyBoolen.daily100Million = true;
        }
        if (me.dailyGetFishes && me.dailyGetFishes >= 1000) {
            dailyBoolen.daily1000F = true;
        }
        if (me.dailyGetFishes && me.dailyGetFishes >= 5000) {
            dailyBoolen.daily5000F = true;
        }
        if (me.dailyGetFishes && me.dailyGetFishes >= 10000) {
            dailyBoolen.daily10000F = true;
        }
        var onlineTime = parseInt((this.now - me.loginTime) / (1000 * 60)); //   
        if (me.onlineTime)
            onlineTime += parseInt(me.onlineTime); //          &   
        if (onlineTime >= 90) {
            dailyBoolen.online90 = true
        }
        if (onlineTime >= 120) {
            dailyBoolen.online120 = true
        }
        var pro3Value = 0;
        for (var z = 0; z < list3._children.length; z++) {
            list3.getChildAt(z).getChildAt(0).getChild('z1').text = +eval(orderText[z]);
            if (dailyBoolen[order[z]]) {
                list3.getChildAt(z).getChildAt(0).getController('c1').selectedIndex = 1;
                pro3Value += points[z];
            } else {
                (function(z) {
                    list3.getChildAt(z).getChildAt(0).getChild('x1').onClick(null, self.dailyclick, [action[z], self])
                })(z)
            }
        }
        var list3 = daily.getChild('n114').text = pro3Value.toString();


        pro3.value = pro3Value / 2.1
        if (pro3Value >= 20) {
            pro3.value = 15 + pro3Value - 20;
            if (daily.getController('c1').selectedIndex == 0) {
                daily.getController('c1').selectedIndex = 1;
                daily.getChild('n126').onClick(null, function() {
                    _socket.sendp({ c: 'gift2everyday', stage: 1 });
                });
                console.log(self)
                    // self.getController('c6').selectedIndex = 0;
            }
            if (me.dailyGift && me.dailyGift[0]) {
                daily.getController('c1').selectedIndex = 2;
            }
        }
        if (pro3Value >= 40) {
            pro3.value = 35 + (pro3Value - 40) * 0.5;
            if (daily.getController('c2').selectedIndex == 0) {
                daily.getController('c2').selectedIndex = 1;
                daily.getChild('n130').onClick(null, function() {
                    _socket.sendp({ c: 'gift2everyday', stage: 2 });
                });
                // self.getController('c6').selectedIndex = 0;
            }
            if (me.dailyGift && me.dailyGift[1]) {
                daily.getController('c2').selectedIndex = 2;
            }
        }
        if (pro3Value >= 80) {
            pro3.value = 55 + (pro3Value - 80) * 0.5;
            if (daily.getController('c3').selectedIndex == 0) {
                daily.getController('c3').selectedIndex = 1;
                daily.getChild('n367').onClick(null, function() {
                    _socket.sendp({ c: 'gift2everyday', stage: 3 });
                });
                // self.getController('c6').selectedIndex = 0;
            }
            if (me.dailyGift && me.dailyGift[2]) {
                daily.getController('c3').selectedIndex = 2;
            }
        }
        if (pro3Value >= 120) {
            pro3.value = 75 + (pro3Value - 120) * 0.5;
            if (daily.getController('c4').selectedIndex == 0) {
                daily.getController('c4').selectedIndex = 1;
                daily.getChild('n371').onClick(null, function() {
                    _socket.sendp({ c: 'gift2everyday', stage: 4 });
                });
                // self.getController('c6').selectedIndex = 0;
            }
            if (me.dailyGift && me.dailyGift[3]) {
                daily.getController('c4').selectedIndex = 2;
            }
        }
        if (pro3Value >= 160) {
            pro3.value = 95 + (pro3Value - 160) * 0.1;
            if (daily.getController('c4').selectedIndex == 0) {
                daily.getController('c5').selectedIndex = 1;
                daily.getChild('n142').onClick(null, function() {
                    _socket.sendp({ c: 'gift2everyday', stage: 5 });
                });
                // self.getController('c6').selectedIndex = 0;
            }
            if (me.dailyGift && me.dailyGift[4]) {
                daily.getController('c5').selectedIndex = 2;
            }
        }
    }
    aftercheck() {
        var self = this;
        var lian = 0,
            leiji = 0;
        for (var x = 0; x < 7; x++) {
            var dat = this.today - 1 - x;
            if (dat < 0) {
                dat = 38 + dat;
            }
            if (me.month[dat] == 2) {
                ++lian;
            } else {
                break;
            }
        }
        for (var x = 0; x < 31; x++) {
            leiji += (me.month[x] - 1)
            var a = 52 + this.firstOfMonth + x;
            if (a > 58) {
                a += 1
            }
            if (me.month[x] == 2) {
                var qianpng = fairygui.UIPackage.createObjectFromURL('ui://7sthu7yatwcm6l');
                qianpng.x = self.contentPane.getChild('n13').getChild('n' + a).x;
                qianpng.y = self.contentPane.getChild('n13').getChild('n' + a).y - 2;
                self.contentPane.getChild('n13').addChild(qianpng);
            }
        }
        if (me.month[this.today - 1] == 2) {
            self.contentPane.getController('c4').selectedIndex = 1;
        }
        self.contentPane.getChild('n13').getChild('n42').text = lian.toString();
        self.contentPane.getChild('n13').getChild('n43').text = leiji.toString();
        var daily = this.contentPane.getChild('n113');
        console.log('aftercheck')
        if (self.contentPane.getController('c4').selectedIndex == 1 && self.contentPane.getController('c5').selectedIndex == 1) {
            mainview.getController('c2').selectedIndex = 0;
        }

        self.dailyinit(daily)
    }
