function resetCoin() {
    if (me.coins > 100000000) {
        var a = (me.coins / 100000000).toFixed(1);
        if (a[a.length - 1] == 0) {
            return parseInt(a) + '';
        } else {
            return a + '';
        }
    } else if (me.coins > 1000000) {
        var a = parseInt(me.coins / 10000)
        return a + '';
    } else {
        return me.coins.toString();
    }
}

