class VipWin extends Win {
    constructor() {
        super('');
    }

    onInit() {
        super.onInit();

        var self = this;
        self.contentPane.getController('c1').selectedIndex = 1;
        var nowPlane = me.vipLv - 1;
        if (nowPlane < 0)
            nowPlane = 1;
        var list = this.contentPane.getChild('n119');
        list.scrollToView(nowPlane);
        this.contentPane.getChild('n2').onClick(null, function() { self.hide() });
        this.contentPane.getChild('n9').onClick(null, function() {
            var shop = new ShopingWin(1);
            shop.modal = true;
            shop.show();
        });
        var proBar1 = this.contentPane.getChild('n13');
        var vipM = [0, 5, 15, 35, 60, 95, 145, 244, 433, 678, 977]
        self.contentPane.getChild('n17').text = (vipM[+me.vipLv + 1] - me.savedMoney).toString()
        self.contentPane.getChild('n18').text = (me.vipLv + 1).toString()
        proBar1.value = (me.savedMoney - vipM[+me.vipLv]) / (vipM[+me.vipLv + 1] - vipM[+me.vipLv]) * 100
        if (me.vipLv == 10) {
            self.contentPane.getChild('n14').visible = false;
            self.contentPane.getChild('n17').visible = false;
            self.contentPane.getChild('n16').visible = false;
            self.contentPane.getChild('n15').visible = false;
            self.contentPane.getChild('n18').visible = false;
            proBar1.value = 100;
        }

        self.contentPane.getChild('n11').text = me.vipLv.toString();
        if (nowPlane == 0) {
            self.contentPane.getChild('n22').visible = false;
        }
        if (nowPlane == 9) {
            self.contentPane.getChild('n21').visible = false;
        }

        self.contentPane.getChild('n21').onClick(null, function() {
            nowPlane++;
            if (nowPlane > 9) { nowPlane = 9 }
            list.scrollToView(nowPlane, 1);
            if (nowPlane == 0) {
                self.contentPane.getChild('n22').visible = false;
            } else {
                self.contentPane.getChild('n22').visible = true;
            }
            if (nowPlane == 9) {
                self.contentPane.getChild('n21').visible = false;
            }
        });

        self.contentPane.getChild('n22').onClick(null, function() {
                nowPlane--;
                if (nowPlane < 0) { nowPlane = 0 }
                list.scrollToView(nowPlane, 1);
                if (nowPlane == 0) {
                    self.contentPane.getChild('n22').visible = false;
                }
                if (nowPlane == 9) {
                    self.contentPane.getChild('n21').visible = false;
                } else {
                    self.contentPane.getChild('n21').visible = true;

                }
            })
            // netmsg.once('board', null, this._nmh);
    }
    hide() {
        super.hide();
        // netmsg.off('board', null, this._nmh);
    }
}

class ShowMailWin extends Win {
    constructor() {
        super('');
        this._ack = {};
    }
    onInit() {
        super.onInit();
        this.contentPane.getChild('n6').onClick(null, function() { self.hide() });
        var _l = this.contentPane.getChild('n43');
        _l.removeChildren();
        var self = this;
        _socket.sendp({ c: 'mails', start: 0 });
        this._nmh = function(pack) {
            console.log(pack)
            var mailLength = pack.v.length;
            if (mailLength > 0) {
                self.contentPane.getController('c1').selectedIndex = 1;
            };
            for (let i = 0; i < pack.v.length; i++) {
                let item = pack.v[i];
                var mail = fairygui.UIPackage.createObject('Package1', '');
                // mail.getChild('n7').text = (item.from ? (item.from + '') : '') + toItemString(item.content);
                mail.mailId = item.id;
                mail.getChild('n15').onClick(null, function(_item) {
                    // if (item.content.tickets) tongji.reward(item.content.tickets, item.from);
                    _socket.sendp({ c: 'rcvmail', mailid: item.id });
                    self._ack[item.id] = i;
                }, [item]);
                _l.addChild(mail);
            }
        }
        netmsg.once('mails', null, this._nmh);
        netmsg.on('rcvmail', this, this._mailrcvHandler);
    }
    _mailrcvHandler(pack) {
        console.log(pack);
        var getc = new CoinWin('', pack);
        getc.modal = true;
        getc.show();
        var _l = this.contentPane.getChild('n43');
        for (let i = 0; i < _l._children.length; i++) {
            if (_l.getChildAt(i).mailId == pack.mailid) {
                _l.removeChildAt(i)
            }
        }
        if (_l._children.length == 0) {
            mainview.getController('c1').selectedIndex = 0;
        }

        // if (pack.status != 'ok') return;
        // var slot = this._ack[pack.mailid];
        // if (slot == null) return;
        // this.contentPane.getChild('n43').asList.removeChildAt(slot);
    }
    hide() {
        super.hide();
        netmsg.off('mails', this._nmh);
        netmsg.off('rcvmail', this, this._mailrcvHandler);
    }
}




