class HighWin extends Win {
    constructor(data) {
        super('');
        this.data = data;
    }
    onInit() {
        super.onInit();
        var self = this;
        this.contentPane.getChild('n3').onClick(null, function() { self.hide() });
        var list1 = this.contentPane.getChild('n40'); //
        var name = [24, 27, 29, 31];
        var coinSet = [34, 35, 36, 37];
        var joinSet = [26, 28, 30, 32];
        list1.removeChildren();
        for (var z = 0; z < this.data.length + 3; z++) {
            var item = fairygui.UIPackage.createObject('Package1', '').asCom;
            for (var y = 0; y < 4; y++) {
                if (this.data[z] && this.data[z].pack.seat[y]) {
                    // item.getChild(('n' + coinSet[y])).text = (parseInt(Math.random() * 300000) + 50000).toString();
                    // item.getChild(('n' + name[y])).text = (parseInt(Math.random() * 400)).toString();
                    item.getChild(('n' + coinSet[y])).text = this.data[z].pack.seat[y].coins.toString();
                    item.getChild(('n' + name[y])).text = this.data[z].pack.seat[y].id.toString();
                } else {
                    item.getChild(('n' + name[y])).text = null;
                    item.getChild(('n' + coinSet[y])).text = null;
                    item.getController('c' + (y + 1)).selectedIndex = 1;
                    (function(y, z) {
                        item.getChild(('n' + joinSet[y])).onClick(null, function() {
                            if (z) {
                                self.hide();
                                _socket.sendp({ "c": "join", "code": z.id });
                            } else {
                                _socket.sendp({ "c": "createServer", roomtype: 'fish', opt: { type: 'gao' } });
                            }
                        });
                    })(y, this.data[z]);
                }
            }
            list1.addChild(item)
        }

        this.contentPane.getChild('n19').onClick(null, function() { //
            if (this.data && this.data[0]) {
                self.hide();
                _socket.sendp({ "c": "join", "code": this.data[0].id });
            } else {
                _socket.sendp({ "c": "createServer", roomtype: 'fish', opt: { type: 'gao' } });
            }
        })
        this.contentPane.getChild('n21').onClick(null, function() { //

        })
        this.join = function(pack) {
            self.hide();
            _socket.sendp({ "c": "join", "code": pack.id });
        }


        netmsg.on('createServer', null, this.join);
    }
    hide() {
        super.hide();
        netmsg.off('createServer', null, this.join);
    }
}
